@import "../../../../common/styles/sass/variables";
@import "../../../../common/styles/sass/mixins";

.tableRow{
  border-radius: 0;
  margin: 0;
  padding: 0;
  @include text(14px, $textColor);
  height: 60px !important;


  .tableCell{
    margin: 0;
    padding: 0;
    @include text(14px, $textColor);
    border-bottom: 1px solid $borderColor;
  }
}